import Vue from "vue";
import VueRouter from "vue-router";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "DashLayout",
    component: () => import("../components/DashLayout.vue")
  },
  {
    path: "/profile",
    name: "systemProfile",
    component: () => import("../views/SystemProfile.vue")
  },
  {
    path: "/add-member",
    name: "AddMember",
    component: () => import("../views/AddMember.vue")
  },
  {
    path: "/SystemToolTip",
    name: "Tips",
    component: () => import("../views/SystemTooltip.vue")
  },
  {
    path: "/auth",
    name: "Auth",
    component: () => import("../views/Auth.vue")
  },
  {
    path: "/Alerts",
    name: "Alerts",
    component: () => import("../views/Alerts.vue")
  }
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes
});

export default router;
