export default {
  fetchLogin: async (password, email) => {
    let body = { password: password, email: email };
    try {
      const result = await fetch("https://melytix.herokuapp.com/login", {
        method: "POST",
        headers: {
          "Content-Type": "application/json;charset=utf-8"
        },
        body: JSON.stringify(body)
      })
        .then(res => res.json())
        .then(data => {
          return data;
        });
      return result;
    } catch ({ message }) {
      return { Error: message };
    }
  },
  fetchSignUp: async (email, password) => {
    let body = { password: password, email: email };
    try {
      const result = await fetch("https://melytix.herokuapp.com/registration", {
        method: "POST",
        headers: { "Content-Type": "application/json;charset=utf-8" },
        body: JSON.stringify(body)
      })
        .then(res => {
          return res.json();
        })
        .then(data => {
          return data;
        });
      return result;
    } catch ({ message }) {
      return { status: "success", email: email };
    }
  },
  googleAuth: () => {
    return "b08f86af-35da-48f2-8fab-cef3904660bd";
  },
  facebookAuth: () => {
    return "b08f86af-35da-48f2-8fab-cef3904660bd";
  },
  forgotPass: email => {
    const answ = email === "";
    return answ;
  }
};
