import Vue from "vue";
import Vuex from "vuex";
import dashboards from "./modules/dashboards.js";
import dateRange from "./modules/dateRange.js";
import Tip from "./modules/Tips.js";
import auth from "./modules/auth.js";
import Alert from "./modules/Alerts.js";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    isModal: false
  },
  mutations: {
    OPEN_MODAL: state => {
      state.isModal = true;
    },
    CLOSE_MODAL: state => {
      state.isModal = false;
    }
  },
  modules: {
    dashboards,
    dateRange,
    Tip,
    auth,
    Alert
  }
});
