  margin-left: 20px;
}

.dropdown:hover {
  box-shadow: 4px 4px 7px 1px rgba(0, 0, 0, 0.3),
    -3px -3px 7px 1px rgba(255, 255, 255, 0.2);
  opacity: 1;
}

