<template>
  <div class="top-panel">
    <div class="add-btn">
      <img src="../assets/images/top_panel_btn/plus.svg" alt="plus" @click="OPEN_MODAL" />
    </div>
    <div class="top-btn">
      <img src="../assets/images/top_panel_btn/copy.svg" alt="copy" @click="copyWidget" />
    </div>
    <div class="top-btn">
      <img src="../assets/images/top_panel_btn/delete.svg" alt="delete" @click="deleteWidget" />
    </div>
    <div class="top-btn">
      <img src="../assets/images/top_panel_btn/share.svg" alt="share" />
    </div>
    <div class="top-btn">
      <img src="../assets/images/top_panel_btn/download.svg" alt="download" />
    </div>
  </div>
</template>

<script>
import { mapMutations, mapGetters } from "vuex";
export default {
  methods: {
    ...mapMutations(["OPEN_MODAL"]),
    ...mapMutations("dashboards", ["DELETE_WIDGET", "ADD_NEW_WIDGET"]),

    deleteWidget() {
      const w = this.getSelectedWidget;
      w ? this.DELETE_WIDGET(w.name) : false;
    },

    copyWidget() {
      const w = this.getSelectedWidget;
      if (w) {
        switch (true) {
          case w.name.includes("signups"):
            w.anSite = "ga";
            break;

          case w.name.includes("revenu"):
            w.anSite = "revenu";
            break;

          default:
            break;
        }
        this.ADD_NEW_WIDGET(w);
      }
    }
  },

  computed: {
    ...mapGetters("dashboards", ["getSelectedWidget"])
  }
};
</script>

