export default function randomDate(
  dateMin = new Date() - 24 * 7 * 60 * 60 * 1000,
  dateMax = new Date()
) {
  console.log(dateMin);
  function getRandomArbitrary(min, max) {
    return Math.random() * (max - min) + min;
  }
  let date1 = dateMin || "01-01-2020";
  let date2 = dateMax || new Date().toLocaleDateString();
  date1 = new Date(date1).getTime();
  date2 = new Date(date2).getTime();

  let time1 = parseInt(getRandomArbitrary(24, 0));
  let time2 = parseInt(getRandomArbitrary(59, 0));
  time1 < 10 ? (time1 = "0" + time1) : time1;
  time2 < 10 ? (time2 = "0" + time2) : time2;
  if (date1 > date2) {
    return {
      date: new Date(getRandomArbitrary(date2, date1)).toLocaleDateString(),
      time: `${time1}:${time2}`
    };
  } else {
    return {
      date: new Date(getRandomArbitrary(date1, date2)).toLocaleDateString(),
      time: `${time1}:${time2}`
    };
  }
}
