import api from "@/api/auth";

export default {
  namespaced: true,
  state: {
    authData: {
      email: "",
      password: ""
    },
    token: localStorage.getItem("token") || "",
    email: localStorage.getItem("email") || "",
    isMessage: false,
    message: "",
    isSignup: true,
    isLogin: false,
    user: "",
    forgotPassStatus: false,
    isFetching: false,
    authTemplate: {
      email: "",
      password: ""
    }
  },
  mutations: {
    LOGOUT: state => {
      localStorage.removeItem("token");
      localStorage.removeItem("email");
      state.isLogin = false;
      state.isSignup = true;
      state.token = "";
      state.email = "";
      state.message = "";
      state.authData = Object.assign({}, state.authTemplate);
    },
    LOGIN: async (state, rout) => {
      const { email, password } = state.authData;
      state.isFetching = true;
      const answ = await api.fetchLogin(password, email).then(data => {
        state.isFetching = false;
        return data;
      });
      const { Error, token } = answ;
      if (Error) {
        state.message = Error;
        state.isMessage = true;
        return;
      }
      if (token) {
        localStorage.setItem("token", token);
        localStorage.setItem("email", email);
        state.token = token;
        state.isLogin = true;
        state.authData = Object.assign({}, state.authTemplate);
        rout();
        return;
      }
    },
    SOCIAL_LOGIN: (state, data) => {
      const { token, Error } = data;
      if (Error) {
        state.message = Error;
        state.isMessage = true;
        return;
      }
      if (token) {
        localStorage.setItem("token", token);
        state.token = token;
        state.isLogin = true;
        return;
      }
    },
    SIGNUP: async state => {
      const { email, password } = state.authData;
      state.isFetching = true;
      const answ = await api.fetchSignUp(email, password).then(data => {
        state.isFetching = false;
        return data;
      });
      const { Error, status } = answ;
      if (Error) {
        state.authData = { email: answ.email, password: "" };
        state.message = Error;
        state.isMessage = true;
      }
      if (status === "success") {
        state.authData = { email: answ.email, password: "" };
        state.message = status;
        state.isMessage = true;
        state.isSignup = false;
      }
    },
    FORGOTPASS: (state, email) => {
      const newPass = api.forgotPass(email);
      state.forgotPassStatus = newPass;
    },
    SET_ISSIGNUP: state => {
      state.isSignup = !state.isSignup;
    },
    DEL_AUTHMESSAGE: state => {
      state.message = "";
      state.isMessage = false;
    }
  },
  getters: {
    authData() {
      return this.authData;
    },
    token(state) {
      return state.token;
    },
    message(state) {
      return state.message;
    },
    user(state) {
      return state.user;
    }
  }
};

